@font-face {
    font-family: "Maiandra GD";
    font-weight: normal;
    src: url("./assets/Maiandra-GD-Regular.ttf");
}

* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html, body {
    font-family: "Maiandra GD";
    background-color: #212121;
    color: #fff;
    position: relative;
}

a {
	color: #a8a7a8;
	text-decoration: none;
}

a:hover, a:active {
	text-decoration: underline;
}



