.Form .columns {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 20px;
}

.Form .column {
    margin-right: 10px;
}

.Form .canvas {
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
}

.Form input {
    -webkit-box-shadow: inset 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
            box-shadow: inset 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
    background-color: #424242;
    border: 1px solid black;
    color: #fff;
    display: block;
    margin-bottom: 4px;
    padding-left: 4px;
}


